/* width */
::-webkit-scrollbar {
  height: 8px;
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba( 0, 0, 0, .4); 
  border-radius: 100px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #52abf6; 
  border-radius: 100px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #78bdf7; 
}

.sticky {
	display: block;
}

#page {
	position: relative;
	min-height: 100%;
}

#content {
	margin: 0;
	padding: 0;

	main#main, main#site-main {
		width: 100%;
		max-width: 1200px;
		margin: 15px auto;
		padding: 0 15px;

		header.entry-header {
			display: flex;
			align-items: center;
			justify-content: center;
		}

		article .woocommerce {
			padding: 0 15px;
		}
	}

	main#main.main-shop-instagram {
		width: 100%;
		max-width: 1500px;
	}

	main.main-contact-us {

		.entry-title {
			display: none;
		}
	}
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	p {
		line-height: 1.1;
	}
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

section {

	&.error-404 {
		max-width: 1200px;

		.widget {

			ul {
				list-style: none;
				margin: 0;
			}
		}
	}
}

body.single-post {

	div#content {
		position: relative;
	}

	#primary {
		position: relative;
		display: flex;
		align-items: stretch;
		justify-content: center;
		flex-wrap: wrap;

		main#main.main-post {
			position: relative;
			flex: .8 0 80%;

			article.post {
				position: relative;

				header.entry-header {
					position: relative;
					padding: 0;
					min-height: 85px;
					background: rgba( #78bdf7, .4 );
					@include prefix( box-shadow, 0 1px 4px 0 rgba( #000000, .2 ), o moz webkit );

					div.title-wrap {
						position: absolute;
						display: flex;
						align-items: center;
					    top: 0;
					    left: 0;
					    padding: 8px;
					    height: 65px;
					    background: #fff;
						
						.entry-title {
							margin: 0;
							padding: 8px;
						}
						
						&:after {
							content: "";
							position: absolute;
							display: block;
							height: 15px;
							top: 0;
							left: 100%;
							width: 32.5px;
							height: 32.5px;
							border: 32.5px solid #fff;
							border-bottom-color: transparent;
							border-right-color: transparent;
						}
					}

					div.meta-wrap {
						position: absolute;
						display: flex;
						align-items: center;
						flex-wrap: wrap;
					    bottom: 0;
					    right: 0;
					    padding: 8px;
					    width: 45%;
					    height: 65px;
					    background: #f1f1f1;
					    z-index: 50;

					    .entry-meta {
					    	flex: 1 0 70%;
					    }

					    &:before {
					    	content: "";
					    	position: absolute;
					    	display: block;
					    	height: 15px;
					    	bottom: 0;
					    	right: 100%;
					    	width: 32.5px;
					    	height: 32.5px;
					    	border: 32.5px solid #f1f1f1;
					    	border-top-color: transparent;
					    	border-left-color: transparent;
					    }
					}

					&:before {
						content: "";
						position: absolute;
						display: block;
						height: 15px;
						top: 0;
						width: 100%;
						background: #fff;
					}

					&:after {
						content: "";
						position: absolute;
						display: block;
						height: 15px;
						bottom: 0;
						width: 100%;
						background: #f1f1f1;
					}
				}
			}

			@include media-breakpoint( md ) {
				flex: 0 1 60%;
			}
		}

		aside#secondary {
			flex: .8 1 80%;
			margin: 15px auto;
			padding: 0 15px;

			ul {
				list-style: none;
				margin: 0;
				padding: 0;
			}

			section.widget {
				padding: 15px;
				@include prefix( box-shadow, 0 1px 4px 0 rgba( #000, .2 ), o moz webkit );

				.menu-media-assets-container {
					padding: 8px;
					border-top: 1px solid rgba( #000, .2 );
				}
			}

			@include media-breakpoint( md ) {
				flex: .25 1 25%;
				border-left: 1px solid rgba( #333333, .1 );
			}
		}
	}
}

body.search {

	article.product {
		margin-bottom: 15px;
	}
}

#searchform {
	margin: 15px;
	justify-content: flex-end;

	input#s {
		border-radius: 100px;
		border-width: 1px;
		padding: 8px;
		line-height: 1.2;
		height: 100%;
		border-radius: 100px 0 0 100px;
		@include font-size( 1.1 );

		&::placeholder {
			font-style: italic;
		}
	}

	button.wonka-btn {
		padding: 8px 15px;
		border-radius: 100px;
		line-height: 1.2;
		text-transform: none;
		height: 100%;
		border-radius: 0 100px 100px 0;
		@include font-size( 1.1 );

		i {
			color: rgba( #fff, .8 );
			@include prefix( transition, $trans_25, o moz webkit );
		}

		&:hover {

			i {
				color: #ffffff;
			}
		}
	}

	@include media-breakpoint( md ) {

		input#s {
			border-radius: 100px 0 0 100px;
		}

		button.wonka-btn {
			padding: 8px 15px;
			border-radius: 0 100px 100px 0;
		}
	}
}

/*=====================================
=            Search styles            =
=====================================*/
/* The overlay effect with black background */
.overlay {
	height: 100%;
	width: 100%;
	display: none;
	position: fixed;
	z-index: 99999;
	top: 0;
	left: 100%;
	opacity: 0;
	background-color: rgb(0,0,0);
	background-color: rgba(0,0,0, 0.9); /* Black with a little bit see-through */
	@include prefix( transition, $trans_25, o moz webkit );
}

/* The content */
.overlay-content {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	top: 46%;
	width: 100%;
	text-align: center;
	margin-top: 30px;
	margin: auto;
	overflow: hidden;
	@include prefix( transition, $trans_25, o moz webkit );

	form#searchform {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		width: 100%;

		/* Style the search field */
		input[type=text] {
			width: 70%;
			padding: 13px;
			@include font-size( 1.1 );
			@include prefix( transition, $trans_25, o moz webkit );
		}

		input[type=text]:hover {
			background: #f1f1f1;
		}

		button {
			width: auto;
			margin-top: 0px;
			margin-left: 0;
			@include prefix( transition, $trans_25, o moz webkit );
		}
	}
	@include media-breakpoint( md ) {
		width: 80%;
		form#searchform {
			width: 65%;

			input[type=text] {
				width: 50%;
			}
			button {
				width: auto;
				margin-top: 0;
				margin-left: -2px;
			}
		}
	}
}

/* Close button */
.overlay .closebtn {
	position: absolute;
	top: 20px;
	right: 45px;
	font-size: 60px;
	cursor: pointer;
	color: white;
	@include prefix( transition, $trans_25, o moz webkit );
}

.overlay .closebtn:hover {
	color: #ccc;
}

div.wonka-gform_wrapper {

	label.gfield_label {
		display: none;
	}

	ul.gform_fields {
		list-style: none;
		margin: 0;
		padding: 0;

		li.wonka-form-control {
			border: none;
		}
	}
}

div.autocomplete-suggestions {
	margin-top: 8px;
	border-radius: 4px;
	z-index: 99999;

	div.autocomplete-suggestion {
		padding: 8px;
		background: transparent;
		@include prefix( transition, $trans_25, o moz webkit );

		&:hover {
			background: rgba( #000, .2 );
		}
	}
}
/*=====  End of Search styles  ======*/
